.App {
  text-align: center;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.CSV{
  background: #282c34;
  margin: 0;
}
.Header{
  text-align: center;
  background-color: #282c34;
  height: auto;
  width: auto;

  
}
.Header>h1{
  color: wheat;
}
.Header>input{
  cursor: pointer;
  padding-bottom: 10px;
  margin-top: 2%;
  font-size: 1rem;
  border-radius: 10px;
  width: auto;
  height: auto;
  color: #f3f3f3;
  
  
}
::-webkit-file-upload-button{
  cursor: pointer;
  color: rgb(40, 38, 36);
  background: #f3f3f3;
  padding: 20px;
  border: none;
  border-radius: 50px;
  outline: none;
}

.H1{
font-size: 2rem;
font-weight:400;
}

/* APP TABLE CSS */
.TableMain{
  border-collapse: collapse;
}

.Thead1>tr>th{
  
  border: 2px solid rgb(201, 201, 201);

 }

.Select_opt{
  padding: 5px;
  font-size: 1rem;
  width: 10rem;
  border: 0.1px solid rgb(223, 221, 221);
}
.Tbody1>tr>td{  
  border: 2px solid rgb(201, 201, 201);
  text-align:center;
  background-color: #ffffff;
  color: rgb(25, 22, 22);
  font-weight: 400;
  padding: 5px;
}
.finish{
  cursor: pointer;
  width: 10%;
  color: rgb(40, 38, 36);
  background: #f3f3f3;
  padding: 20px;
  border: none;
  border-radius: 50px;
  outline: none;
  font-size: 1rem;
  transition: 0.6s;
  font-weight: 500;
  

}
.finish:hover{
  background-color: #12404d;
  color: #ffff;
  transition: 0.6s;
}
::-webkit-file-upload-button:hover{
  background-color: #12404d;
  color: #ffff;
  transition: 0.6s;
}
input{
  width: 12%;
}


